@import "../../styles/colors.scss";

.dosAndDontsWrapper {
  height: 100vh;
  align-items: center;
}

.dosDontsContent{
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  padding: 4rem;
  background-color: #FBFBFB;
  box-shadow: 0px 1px 6px #00000029;
  h5 {
    font-weight: bold;
    font-size: 3.125rem;
    color: $grey-4;
    margin-bottom: 1rem;
    letter-spacing: 0.9px;
    span {
      font-size: 1rem;
      vertical-align: middle;
    }
  }
  ul {
    li {
      font-weight: bold;
      font-size: 1.2rem;
      line-height: 1.8;
      color: $grey-1;
      a,
      a:active {
        color: purple;
      }
    }
  }
}
